import React, {Component} from 'react'
import MediaQuery from 'react-responsive'
import {Fullpage, Slide} from './fullpage-react'
import ReactGA from 'react-ga'
import './App.css'
import Menu from './components/Menu'
import Contact from './components/Contact'
import Team from './components/Team'
import Context from './components/Context'
import Products from './components/Products'
import Science from './components/Science'
import BG from './components/BG'
import How from './components/How'

import Main from './components/Main'
import Mission from './components/Mission'

if (process.env.GA_TRACKING_ID) {
  ReactGA.initialize(process.env.GA_TRACKING_ID, {
    debug: true,
    titleCase: false
  })
}

const fullPageOptions = {
  // for mouse/wheel events
  // represents the level of force required to generate a slide change on non-mobile, 10 is default
  scrollSensitivity: 7,
  
  // for touchStart/touchEnd/mobile scrolling
  // represents the level of force required to generate a slide change on mobile, 10 is default
  touchSensitivity: 7,
  scrollSpeed: 1,
  hideScrollBars: true,
  enableArrowKeys: true,
  activeSlide: 0
}

const names = [
  {className: 'main', backgroundColor: '#042a85'},
  {className: 'mission', backgroundColor: '#1e1e1e'},
  {className: 'context', backgroundColor: '#042a85'},
  {className: 'science', backgroundColor: '#1e1e1e'},
  {className: 'products', isLight: true, backgroundColor: '#ffffff'},
  {className: 'team', backgroundColor: '#1e1e1e'},
  {className: 'contacts', backgroundColor: '#fc2622'}
]

const slides = [
    <Main index={0}/>
  ,
    <Mission index={1}/>
  ,
    <Context index={2}/>
  ,
    <Science index={3}/>
  ,
    <Products index={4}/>
  ,
    <Team index={5}/>
  ,
    <Contact index={6}/>
]

fullPageOptions.slides = slides.map((content) => <Slide className="hq-slide">{content}</Slide>)

class App extends Component {
  state = {
    active: {
      Fullpage: 0, data: names[0]
    }
  }
  
  componentDidMount() {
    this.lastSlideChangeTime = Date.now()
    requestAnimationFrame(() => {
      const slide = document.querySelectorAll('.hq-slide')
      if (slide.length) {
        slide[this.state.active.Fullpage].classList.add('active-slide')
      }
      if (window.innerWidth < 1024) {
        const header = document.querySelector('header');
        if (header) {
          header.style.backgroundColor = names[this.state.active.Fullpage].backgroundColor;
        }
      }
    })
  }
  
  onSlideChangeStart = (name, props, state, newState) => {
    // since we use very small fullPageOptions.scrollSpeed value, it is easy to scroll
    // past the slide to counter that we shall require minimum slide time here
    let time = Date.now()
    if (time - this.lastSlideChangeTime < 1500) {
      return true
    }
    if (state.scrollPending) {
      this.lastSlideChangeTime = time
    }
    
    document.body.classList.add('body-animation')
    
    const prev = names[newState.lastActive]
    const next = names[newState.activeSlide]
    
    if (prev) {
      document.body.classList.remove('body-' + prev.className)
    }
    if (next) {
      document.body.classList.add('body-' + next.className)
    }
    
    if (next.isLight) {
      document.body.classList.add('body-light')
    } else {
      document.body.classList.remove('body-light')
    }
  }
  
  onSlideChangeEnd = (name, props, state, newState) => {
    if (newState.activeSlide == newState.lastActive) return
    
    document.body.classList.remove('body-animation')
    this.setState({active: {Fullpage: newState.activeSlide, data: names[newState.activeSlide]}})

    const event = new CustomEvent('slidechange', {detail: {index: newState.activeSlide}})
    
    window.dispatchEvent(event)
    
    let i, slides = document.querySelectorAll('.hq-slide')
    for (i = 0; i < slides.length; i++) {
      slides[i].classList[i == newState.activeSlide ? 'add' : 'remove']('active-slide')
    }
  }
  
  changeFullpageSlide = (index) => {
    try {
      Fullpage.changeFullpageSlide(index)
    } catch (fullpageIsNotUsed) {
      this.refs[index].scrollIntoView(true);

      const data = names[index] || names[0];

      this.setState({active: {Fullpage: index, data: data}})

      const event = new CustomEvent('slidechange', {detail: {index: index}})
      
      window.dispatchEvent(event)

      document.querySelector('header').style.backgroundColor = data.backgroundColor;
    }
  }

  watchScrollYTimeout = 0;
  watchScrollY = () => {
    let index = Math.floor ((window.scrollY + 73) / window.innerHeight);
    if (index != this.state.active.Fullpage) {
      clearTimeout (this.watchScrollYTimeout);
      this.watchScrollYTimeout = setTimeout(() => {
        const data = names[index] || names[0];

        this.setState({active: {Fullpage: index, data: data}})

        const event = new CustomEvent('slidechange', {detail: {index: index}})
        
        window.dispatchEvent(event)

        document.querySelector('header').style.backgroundColor = data.backgroundColor;
      }, 100)
    }
  }
  
  render() {
    
    const scope = this
    const {active} = this.state

    let windowInnerHeight = 0;

    try {
      if (window.navigator.userAgent.match(/Node\.js/i) && window.reactSnapshotRender) {
        return (
          <noscript><div className="noscript">{slides}</div></noscript>
        )
      }

      windowInnerHeight = window.innerHeight;

      if (window.innerWidth < 1024) {
        document.body.classList.remove('body-main');

        window.removeEventListener('scroll', this.watchScrollY);
        window.addEventListener('scroll', this.watchScrollY);
      }
    } catch (nextjsServer) {
      // oops
    }

    return (
      <div className="app">
        <img className="device-rotate" src={require('./images/device-rotate.png')}/>
        <Menu changeFullpageSlide={this.changeFullpageSlide} active={active.Fullpage}/>
        <MediaQuery query="(max-device-width: 1023px)">
          <div className="prostinya">
            {slides.map((content, index) =>
              <div key={index} ref={index}
                style={{ backgroundColor: names[index].backgroundColor, minHeight: windowInnerHeight + 'px' }}
                className={'body-' + names[index].className + (names[index].isLight ? ' body-light' : '')}>
                  <div className="hq-slide active-slide">{content}</div>
              </div>
            )}
          </div>
        </MediaQuery>
        <MediaQuery query="(min-device-width: 1024px)">
          <Fullpage {...fullPageOptions}
                  onSlideChangeStart={this.onSlideChangeStart}
                  onSlideChangeEnd={this.onSlideChangeEnd}
          />
        </MediaQuery>
        <nav>
          <ul className="pagination"
              style={{
                position: 'fixed',
                right: 40,
                top: '50%',
                transform: 'translateY(-50%)',
                display: 'flex',
                flexDirection: 'column'
              }}>
            {slides.map((item, index) => (
              <li className={`page-item ${index === active.Fullpage ? 'active current' : ''}`}
                  key={`page-item-${index}`}>
                <a className="page-link"
                   onClick={() => this.changeFullpageSlide(index)}
                   style={{border: 0, borderRadius: 0, backgroundColor: 'transparent', textIndent: '-10000px'}}
                   data-tooltip
                >
                  {index + 1}
                </a>
              </li>
            ))}
          </ul>
        </nav>
      </div>
    )
  }
}

export default App



