import TweenLite from 'gsap/TweenLite'

class SnapHelper {
  constructor(component, rowSelector, columnsNumber, maxWidth, submenuSelector) {
  	this.component = component;
  	this.rowSelector = rowSelector;
  	this.columnsNumber = columnsNumber;
    this.maxWidth = maxWidth;
    this.submenuSelector = submenuSelector;
  }

  lastSnapTime = 0;

  forced = { scrollLeft: 0 };

  onScroll = (event) => {
    if (window.innerWidth > this.maxWidth) {
      return
    }

    let row = document.querySelector(this.rowSelector);
    if (row) {
      if (Date.now() - this.lastSnapTime > 1500) {
        let active = Math.round(this.columnsNumber * row.scrollLeft / row.scrollWidth)
        if (active != this.component.state.active) {
          this.component.setState({
            active: active
          });

          this.lastSnapTime = Date.now();
        }
      } else {
        requestAnimationFrame(() => {
          row.scrollLeft = this.forced.scrollLeft;
        });
      }
    }
  }

  onComponentUpdate = () => {
    // https://stackoverflow.com/a/28748160
    requestAnimationFrame(() => {
      if (window.innerWidth > this.maxWidth) {
        return
      }

      let submenu = document.querySelector(this.submenuSelector);
      if (submenu) {
        TweenLite.to(submenu.parentElement, 0.4, { scrollLeft: submenu.children[this.component.state.active].offsetLeft });
      }

      let row = document.querySelector(this.rowSelector);
      if (row) {
        row.removeEventListener('scroll', this.onScroll);
        row.addEventListener('scroll', this.onScroll);

        this.lastSnapTime = Date.now();
        this.forced.scrollLeft = row.scrollLeft;

console.log('>>', this.component.state.active)
        TweenLite.to(this.forced, 0.4, { scrollLeft: Math.round(row.scrollWidth * this.component.state.active / this.columnsNumber),
          onUpdate: () => {
            row.scrollLeft = this.forced.scrollLeft;
          }
        });
      }
    });
  }

}

export default SnapHelper;


