import React, {Component} from 'react'
import MediaQuery from 'react-responsive'
import RandomLetters from '../RandomLetters'
import SnapHelper from '../SnapHelper'

const menuItems = ['The Model', 'Digital Freud', 'Vibes', 'SkillWill', 'Deep Persona']

const itemDescriptions = [
  '<b class="noscript-only">' + menuItems[0] + ':</b> A technology providing an innovative, scientifically based & responsible way of personal profiling through digital footprints collected & stored on a smartphone  allows prediction of various elements of human behavior.',
  '<b class="noscript-only">' + menuItems[1] + ':</b> The principle component of the new personality profiling system. A non-commercial Android application, used to collect & analyze a vast amount of digital footprints, supported by classical personality questionnaires & cognitive tests. Its citizen science module allows recruiting respondents the data collection for scientific purposes. <a href="http://www.digitalfreud.io/">www.digitalfreud.io</a>',
  '<b class="noscript-only">' + menuItems[2] + ':</b> The former Digital Freud module grown into a separate project. A non-commercial b2c iOS application that allows evaluation of friends & public figures in social networks using a set of criteria in simple, yet captivating mechanics.',
  '<b class="noscript-only">' + menuItems[3] + ':</b> An innovative, ai-powered tool of behavioral assessment, based on personal digital footprints analysis. Fast, valid and unbiased way to assess strengths and gaps of individuals and teams. Involving and engaging way to plan personal and team development.',
  '<b class="noscript-only">' + menuItems[4] + ':</b> Solutions that allow creation of new interfaces between human & AI, taking into account the social context & psycho-emotional state of user. Empathy AI will customize services & applications by automatically adapting to users.'
]

const itemPictures = [
  null/*require('../../images/product-logo-1.svg')*/,
  null,
  null,
  null,
  null
]

class Products extends Component {
  state = {
    active: 0,
    isActive: false
  }
  
  componentWillMount() {
    window.addEventListener('slidechange', this.onSlideChange, false)
  }
  
  componentWillUnmount() {
    window.removeEventListener('slidechange', this.onSlideChange, false)
  }
  
  onSlideChange = (event) => {
    this.setState({isActive: event.detail.index === this.props.index})
    if (this.state.isActive) {
      this.setState({
        active: 0
      });
    }
  }

  snapHelper = new SnapHelper(this, '.body-products .item-descriptions', itemDescriptions.length, 1024, '.body-products .active-slide .submenu');

  componentDidUpdate() {
    this.snapHelper.onComponentUpdate();
  }
  
  render() {
    return (
      <div className="scene-container">
        <div className="jumbotron">
          <div className="container">
            <div className="row grid-x grid-margin-x">
              <div className="col cell medium-12 animation-1">
                <h1><RandomLetters label="Projects" start={this.state.isActive}/><span className="noscript-only">Projects</span></h1>
              </div>
            </div>
            <div className="row grid-x grid-margin-x">
              <div className="col cell large-5 animation-2">
                <MediaQuery query="(max-device-width: 1024px)">
                  <div style={{width: '100%', overflowX: 'auto', WebkitOverflowScrolling: 'touch'}}>
                    <ul className="list-group list-group-flush no-bullet submenu">
                      {menuItems.map((item, index) => (
                        <li key={'menu-item-' + index}
                            onClick={() => this.setState({active: index})}
                            className={this.state.active == index ? 'list-group-item submenu-item active' : 'submenu-item list-group-item'}>{item}.</li>
                      ))}
                    </ul>
                  </div>
                </MediaQuery>
                <MediaQuery query="(min-device-width: 1025px)">
                  <ul className="list-group list-group-flush no-bullet">
                    {menuItems.map((item, index) => (
                      <li key={'menu-item-' + index}
                          onClick={() => this.setState({active: index})}
                          className={this.state.active == index ? 'list-group-item active' : 'list-group-item'}>{item}.</li>
                    ))}
                  </ul>
                </MediaQuery>
              </div>
              <div className="col cell large-7 item-descriptions animation-3">
                {itemDescriptions.map((item, index) => (
                  <div key={'item-description-' + index}
                       className={this.state.active == index ? 'item-description active' : 'item-description inactive'}>
                    <div className="row grid-x grid-margin-x">
                    <MediaQuery query="(max-device-width: 1024px)">
                      <div className="col cell medium-12">
                        <h3>{menuItems[index]}.</h3>
                      </div>
                    </MediaQuery>
                    <MediaQuery query="(min-device-width: 1025px)">
                      <div className="col cell medium-8">
                        <h3>{menuItems[index]}.</h3>
                      </div>
                      <div className="col cell medium-4 animation-4">
                        {itemPictures[this.state.active] ?
                          <img src={itemPictures[this.state.active]} alt={menuItems[index]}/>
                          : null}
                      </div>
                    </MediaQuery>
                    </div>
                    <div className="separator-40"></div>
                    <p dangerouslySetInnerHTML={{ __html: item }}></p>
                  </div>
                ))}
              </div>
            </div>
            <div className="paging">
              <div onClick={() => this.setState({active: Math.max(0, this.state.active - 1)})}>&lt;</div>
              <div onClick={() => this.setState({active: Math.min(4, this.state.active + 1)})}>&gt;</div>
            </div>
          </div>
        </div>
      </div>
    )
  }
}

export default Products


