import React from 'react'
import PropTypes from 'prop-types'

const getRandomInt = (min, max) => Math.floor(Math.random() * (max - min + 1)) + min

class RandomLetters extends React.Component {
  static propTypes = {
    label: PropTypes.string,
    stepsForLetter: PropTypes.number,
    stepInterval: PropTypes.number
  }
  
  static defaultProps = {
    stepsForLetter: 3,
    duration: 150
  }

  state = {
    stepInterval: 50
  }
  
  fillIndex = 0
  
  constructor(props) {
    super(props)
    this.state = {
      buffer: Array.from(props.label, this.getRandomChar),
      steps: new Array(props.label.length).fill(0),
      stepInterval: props.duration / props.label.length
    }
  }
  
  componentWillReceiveProps(nextProps) {
    if (nextProps.start) {
      this.start()
    } else {
      this.fillIndex = 0
      this.setState({steps: new Array(nextProps.label.length).fill(0)})
    }
  }
  
  getRandomChar() {
    return String.fromCharCode(getRandomInt(31, 126))
  }
  
  getRandomFillableCharIndex() {
    const {stepsForLetter} = this.props
    const {steps} = this.state
    const fillableIndexes = steps.map((_, i) => _ <= stepsForLetter ? i : null).filter(_ => _ !== null)
    return fillableIndexes[getRandomInt(0, fillableIndexes.length - 1)]
  }
  
  start() {
    this.animate()
    
    const {stepInterval} = this.state
    
    this.timer = setTimeout(() => {
      if (this.state.buffer.join('') === this.props.label) {
        return this.stop()
      }
      this.start()
    }, stepInterval)
  }
  
  stop() {
    clearTimeout(this.timer)
  }
  
  animate() {
    this.rotate()
  }
  
  rotate = () => {
    const buffer = Array.from(this.state.buffer)
    const {label, stepsForLetter} = this.props
    const {steps} = this.state
    let index = this.getRandomFillableCharIndex()
    
    if (index >= 0) {
      const char = this.getRandomChar()
      if (char === label[index]) {
        steps[index] = stepsForLetter
      } else {
        steps[index] += 1
      }
      buffer[index] = char
    } else {
      buffer[this.fillIndex] = label[this.fillIndex]
      this.fillIndex++
      if (this.fillIndex === buffer.length - 1) {
        this.stop()
      }
    }
    
    this.setState({buffer, steps})
  }
  
  render() {
    const {buffer} = this.state
    
    return (
      buffer.map((_, i) => <span key={`letter-${i}`}>{_}</span>)
    )
  }
}

export default RandomLetters


