import React from 'react'
import RandomLetters from '../RandomLetters'

class Contact extends React.Component {
  state = {
    isActive: false
  }

  componentWillMount() {
    window.addEventListener('slidechange', this.onSlideChange, false)
  }

  componentWillUnmount() {
    window.removeEventListener('slidechange', this.onSlideChange, false)
  }

  onSlideChange = (event) => {
    this.setState({isActive: event.detail.index === this.props.index})
  }

  render() { return (
  <div className="scene-container">
    <div className="jumbotron">
      <div className="container">
        <div className="row grid-x grid-margin-x">
          <div className="col cell medium-12 animation-1">
            <h1><RandomLetters label="Contact" start={this.state.isActive}/><span className="noscript-only">Contact</span></h1>
          </div>
        </div>
        <div className="row grid-x grid-margin-x">
          <div className="col cell medium-4 animation-1">
            <h2>Contact us</h2>
            <div className="separator-40 show-for-medium show-for-large"/>
            <p><a href="mailto:letsconnect@humanteq.io">letsconnect@humanteq.io</a><br/><br/>
              </p>
          </div>
          <div className="col cell medium-8 animation-2">
            <div className="row grid-x grid-margin-x">
              <div className="col cell medium-12">
                <h2>Locate us</h2>
                <div className="separator-40 show-for-medium show-for-large"/>
              </div>
            </div>
            <div className="row grid-x grid-margin-x">
              <div className="col cell medium-6">
                <p>Vesenaz, 43 Route de<br/>
                  Thonon<br/>
                  Geneva, 1222,<br/>
                  Switzerland</p>
                  <div className="separator-40 show-for-small hide-for-medium hide-for-large"/>
              </div>
              <div className="col cell medium-6">
                <p>51/1, Leninsky Avenue<br/>
                  Moscow, 119991,<br/>
                  Russia</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
)}}

export default Contact



